import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './entry/login/login.component';
import { SignupComponent } from './entry/signup/signup.component';
import { GamingComponent } from './home/gaming/gaming.component';
import { TempComponent } from './home/gaming/temp/temp.component';
import { VideoComponent } from './home/gaming/video/video.component';
import { DiscoverComponent } from './home/group/discover/discover.component';
import { FeedComponent } from './home/group/feed/feed.component';
import { GroupComponent } from './home/group/group.component';
import { NotificationComponent } from './home/group/notification/notification.component';
import { HomeComponent } from './home/home/home.component';
import { MarketplaceComponent } from './home/marketplace/marketplace.component';
import { PagesComponent } from './home/pages/pages.component';
import { WatchComponent } from './home/watch/watch.component';

const routes: Routes = [
  {path: "", component: LoginComponent},
  {path: "signup", component: SignupComponent},
  {path: "pages", component: PagesComponent,
  children : [
    {
      path: 'home',
      component: HomeComponent,
    },
    {
      path: 'groups',
      component: GroupComponent,
      children : [
        {path:'',component: FeedComponent},
        {path:'feed',component:FeedComponent},
        {path:'discover',component:DiscoverComponent},
        {path:'notification',component:NotificationComponent}]
    },
    {
      path: 'watch',
      component: WatchComponent,
    },
    {
      path: 'marketplace',
      component: MarketplaceComponent,
    },
    {
      path: 'gaming',
      component: GamingComponent,
      children:[
        {
          path:'',
          component:VideoComponent
        },
        {
          path:'video',
          component:VideoComponent
        },
        {
          path:":pagename",
          component:TempComponent
        }
      ]
    },
    {
      path: 'games',
      component: GamingComponent,
      children:[
        {
          path:"",
          component:TempComponent
        },
        {
          path:":pagename",
          component:TempComponent
        }
      ]
    },
    {
      path: 'tournaments',
      component: GamingComponent,
      children:[
        {
          path:"",
          component:TempComponent
        },
        {
          path:":pagename",
          component:TempComponent
        }
      ]
    },
  ]}  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
