import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';

@Component({
  selector: 'app-live',
  templateUrl: './live.component.html',
  styleUrls: ['./live.component.css']
})
export class LiveComponent implements OnInit {

  lastVolPos: number = 0.5;
  volume: number = 0;
  showVolumeControl: boolean = false;
  lives: any = [{
    videourl: "/assets/videos/live/ninja.mp4",
    livecount: "20k",
    imageurl: "https://static01.nyt.com/images/2020/09/11/business/10ninja-print/merlin_145957176_af6e588b-36f1-40ff-9050-41f45ecca6a2-mediumSquareAt3X.jpg",
    gamename: "COD-Modern Warfare",
    channelname: "Ninja",
    website: ""
  },
  {
    videourl: "/assets/videos/live/nickmercs.mp4",
    livecount: "890",
    imageurl: "https://static-cdn.jtvnw.net/jtv_user_pictures/ff13760d-d795-49d9-8f3f-f05035930c81-profile_image-300x300.png",
    gamename: "Fortnite",
    channelname: "Nickmercs",
    website: ""
  },
  {
    videourl: "/assets/videos/live/shroud.mp4",
    livecount: "32.7k",
    imageurl: "https://www.windowscentral.com/sites/wpcentral.com/files/styles/large/public/field/image/2019/10/shroud-twitch-streamer.jpg",
    gamename: "PUBG",
    channelname: "Shroud",
    website: ""
  },
  {
    videourl: "/assets/videos/live/carryminatti.mp4",
    livecount: "12.69k",
    imageurl: "https://qph.fs.quoracdn.net/main-qimg-57acaaf034bb8a486dc06ca2049e634b",
    gamename: "Grand Theft Auto V",
    channelname: "Carryminati ",
    website: ""
  },
  ]
  constructor() {

  }

  ngOnInit(): void {
    this.shuffle(this.lives)
  }

  play = (event: Event) => {
    (<HTMLVideoElement>(<HTMLDivElement>event.target).children[2]).play();

  }
  pause = (event: Event) => {
    this.showVolumeControl=false;
    (<HTMLVideoElement>(<HTMLDivElement>event.target).children[2]).pause();
  }
  toggleVolume = () => {
    if (this.volume) {
      this.volume = 0;
    } else {
      this.volume = this.lastVolPos;
    }
  }

  makeVolumeVisible = () => {
    this.showVolumeControl = true;
    setTimeout(()=>{
      this.showVolumeControl = false;
    }, 3000 )
  }

  trackVolume=()=>{
    if(this.volume!=0){
      this.lastVolPos = this.volume;
    }
  }
  shuffle(array: any) {
    array.sort(() => Math.random() - 0.5);
  }

}
