import { Component, OnInit } from '@angular/core';
import { HomeService } from './home.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  leftBarDatas: any;
  contactDatas: any;
  storiesData: any;
  postDatas: any;
  favouriteDatas: any;
  friendsData: any;
  recentPostData: any;
  fullPage = true;
  friends = false;
  covid = false;
  centerContent = true;
  favourites = false;
  normalPost = true;
  recentPost = false;
  event = false;
  constructor(
    private HS: HomeService
  ) { }

  ngOnInit(): void {
    this.leftBarDatas = this.HS.leftBarDatas;
    this.contactDatas = this.HS.contactDatas;
    this.storiesData = this.HS.storiesData;
    this.postDatas = this.HS.postData;
    this.favouriteDatas = this.HS.favouriteData;
    this.friendsData = this.HS.freindData;
    this.recentPostData = this.HS.recentPost;
  }

  reset(){
      this.covid = false;
      this.centerContent = true;
      this.favourites = false;
      this.fullPage = true;
      this.friends = false;
      this.normalPost = true;
      this.recentPost = false;
      this.event = false;
  }
  URL(val: any){
    if (val === "COVID 19"){
      this.covid = true;
      this.centerContent = false;
      this.favourites = false;
      this.fullPage = true;
      this.friends = false;
      this.normalPost = true;
      this.recentPost = false;
      this.event = false;
    }else if(val === "Favourites"){
      this.covid = false;
      this.centerContent = false;
      this.favourites = true;
      this.fullPage = true;
      this.friends = false;
      this.normalPost = true;
      this.recentPost = false;
      this.event = false;
    }else if(val === "Friends"){
      this.covid = false;
      this.centerContent = false;
      this.favourites = false;
      this.fullPage = false;
      this.friends = true;
      this.normalPost = true;
      this.recentPost = false;
      this.event = false;
    }else if(val === "Most recent"){
      this.covid = false;
      this.centerContent = true ;
      this.favourites = false;
      this.fullPage = true;
      this.friends = false;
      this.normalPost = false;
      this.recentPost = true;
      this.event = false;
    }else if(val === "Events"){
      this.covid = false;
      this.centerContent = false;
      this.favourites = false;
      this.fullPage = true;
      this.friends = false;
      this.normalPost = false;
      this.recentPost = true;
      this.event = true;
    }
    else{
      this.reset();
    }
  }

}
